import { PropTypes } from 'prop-types';
import { useDispatch } from 'react-redux';
import {
  setPersonToFavourite,
  removePersonFromFavourite,
} from '@store/actions';

import iconFavorite from './img/favorite.svg';
import iconFavoriteFill from './img/favorite-fill.svg';
import styles from './PersonPhoto.module.css';

const PersonPhoto = ({
  personPhoto,
  personName,
  personId,
  personFavourite,
  setPersonFavourite,
}) => {
  const dispatch = useDispatch();

  const dispatchFavoritePeople = () => {
    if (personFavourite) {
      dispatch(removePersonFromFavourite(personId));
      setPersonFavourite(false);
    } else {
      dispatch(
        setPersonToFavourite({
          [personId]: {
            name: personName,
            img: personPhoto,
          },
        })
      );
      setPersonFavourite(true);
    }
  };
  return (
    <>
      <div className={styles.container}>
        <img className={styles.photo} src={personPhoto} alt={personName} />
        <button className={styles.favorite} onClick={dispatchFavoritePeople}>
          <img
            src={personFavourite ? iconFavoriteFill : iconFavorite}
            className={styles.favorite__img}
            alt="Add to favorite"
          />
        </button>
      </div>
    </>
  );
};

PersonPhoto.prototypes = {
  personPhoto: PropTypes.string,
  personName: PropTypes.string,
  personId: PropTypes.string,
  personFavourite: PropTypes.bool,
  setPersonFavourite: PropTypes.func,
};
export default PersonPhoto;
