import React, { useEffect, useState, Suspense } from 'react';
import { PropTypes } from 'prop-types';

import { withErrorApi } from '@hoc-helpers/withErrorApi';
// import PeopleList from '@components/PeopleList';
import UiLoading from '@components/UI/UiLoading';
import PeopleNavigation from '@components/PeopleNavigation';
import { getApiRecourse, changeHTTP } from '@utils/network';
import {
  getPeopleId,
  getPeopleImage,
  getPeoplePageId,
} from '@services/getPeopleData';
import { API_PEOPLE } from '@constants/api';
import { useQueryParams } from '@hooks/useQueryParams';

const PeopleList = React.lazy(() => import('@components/PeopleList'));

const PeoplePage = ({ setErrorApi }) => {
  const [people, setPeople] = useState(null);
  const [prevPage, setPrevPage] = useState(null);
  const [nextPage, setNextPage] = useState(null);
  const [counterPage, setCounterPage] = useState(1);

  const query = useQueryParams();
  const queryPage = query.get('page');

  const getRecourse = async (url) => {
    const res = await getApiRecourse(url);

    if (res) {
      const peopleList = res.results.map(({ name, url }) => {
        const id = getPeopleId(url);
        const img = getPeopleImage(id);
        return {
          id,
          name,
          img,
        };
      });
      setPeople(peopleList);
      setPrevPage(changeHTTP(res.previous));
      setNextPage(changeHTTP(res.next));
      setCounterPage(getPeoplePageId(url));
      setErrorApi(false);
    } else {
      setErrorApi(true);
    }
  };

  useEffect(() => {
    getRecourse(API_PEOPLE + queryPage);
  }, []);

  return (
    <>
      <PeopleNavigation
        getRecourse={getRecourse}
        prevPage={prevPage}
        nextPage={nextPage}
        counterPage={counterPage}
      />
      {people && (
        <Suspense fallback={<UiLoading />}>
          <PeopleList people={people} />
        </Suspense>
      )}
    </>
  );
};

PeoplePage.prototypes = {
  setErrorApi: PropTypes.func,
};

export default withErrorApi(PeoplePage);
