import { PropTypes } from 'prop-types';
import { Link } from 'react-router-dom';
import styles from './PeopleNavigation.module.css';
import UiButton from '@ui/UiButton';

const PeopleNavigation = ({ getRecourse, prevPage, nextPage, counterPage }) => {
  const hadleChangePrev = () => getRecourse(prevPage);
  const hadleChangeNext = () => getRecourse(nextPage);
  return (
    <div className={styles.container}>
      {/* <h1 className="header__text">Navigation</h1> */}
      <Link to={`/people/?page=${counterPage - 1}`} className={styles.buttons}>
        <UiButton
          text="Previous"
          onClick={hadleChangePrev}
          disabled={!prevPage}
          // theme='light'
        />
      </Link>
      <Link to={`/people/?page=${counterPage + 1}`} className={styles.buttons}>
        <UiButton 
          text="Next" 
          onClick={hadleChangeNext} 
          disabled={!nextPage} 
        />
      </Link>
    </div>
  );
};

PeopleNavigation.prototypes = {
  getRecourse: PropTypes.func,
  prevPage: PropTypes.string,
  nextPage: PropTypes.string,
  counterPage: PropTypes.number,
};
export default PeopleNavigation;
