import {
  ADD_PEROS_TO_FAVOURITE,
  REMOVE_PEROS_FROM_FAVOURITE,
} from '@store/constants/actionTypes';

export const setPersonToFavourite = (person) => ({
  type: ADD_PEROS_TO_FAVOURITE,
  payload: person,
});

export const removePersonFromFavourite = (personId) => ({
  type: REMOVE_PEROS_FROM_FAVOURITE,
  payload: personId,
});
